<template>
  <div>
    <div class="bodyContent">
      <el-table :data="cart" style="width: 100%" max-height="600">
        <el-table-column align="center" label="删除" width="100">
          <template slot-scope="scope">
            <a @click="deleteItem(scope.row)">
              <i class="el-icon-delete"></i>
            </a>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="img" label="图片" width="180">
          <template slot-scope="{ row }">
            <img :src="row.img" style="width: 50%" />
          </template>
        </el-table-column>
        <el-table-column align="center" prop="name" label="名称">
        </el-table-column>
        <el-table-column align="center" prop="count" label="数量">
          <template slot-scope="{ row }">
            <el-input-number
              style="width: 80%"
              v-model="row.count"
              label=""
              :min="0"
            ></el-input-number>
          </template>
        </el-table-column>
        <el-table-column align="center" prop="price" label="单价">
        </el-table-column>
      </el-table>
      <el-row style="height: 60px; line-height: 60px">
        <el-col :span="4" :offset="16">
          <h3>总价：{{ totalPrice }}</h3>
        </el-col>
        <el-col :span="2" :offset="2">
          <el-button @click="pay" type="primary">结算</el-button>
        </el-col>
      </el-row>
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import http from "@/utils/request.js";
export default {
  components: {},
  computed: {
    totalPrice() {
      if (this.cart.length > 0) {
        const res = this.cart
          .map((item) => item.price * item.count)
          .reduce((i1, i2) => {
            return i1 + i2;
          });
        console.log("res:", res);
        return res;
      }
    },
  },
  data() {
    return {
      cart: [],
      publicPath: process.env.BASE_URL,
      products: [],
      token: localStorage.getItem("token"),
    };
  },
  mounted() {
    console.log(process.env.BASE_URL);
    this.cart = this.$store.state.cart;
  },
  methods: {
    pay() {
      this.$store.commit("updateTotalPrice", this.totalPrice);
      this.$router.push("/pay");
    },
    goCart() {
      this.$router.push("/cart");
    },
    deleteItem(item) {
      console.log("删除：", item);
      this.$confirm("确定要删除吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        this.cart.splice(
          this.cart.findIndex((i) => i.id == item.id),
          1
        );
      });
    },
    addCart(item) {
      const index = this.cart
        .map((item) => item.id)
        .findIndex((i) => i === item.id);
      console.log("index");
      if (index > -1) {
        this.cart[index]["count"]++;
      } else {
        this.cart.push(item);
      }
      this.$message({
        message: "加购成功",
        type: "success",
      });
    },
    login() {
      console.log("=== login ===");
      this.$router.push("/login");
    },
    logout() {
      console.log("=== logout ===");
      this.$confirm("确定要退出登录吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        http
          .post("/api/user/account/logout")
          .then(() => {
            localStorage.clear();
            this.$router.push("/login");
          })
          .catch((err) => {
            console.log("err:", err);
          });
      });
    },
    chat() {
      console.log("=== chat ===");
      this.$router.push("/chat");
    },
  },
};
</script>

